import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
const apiUrl: string = 'https://www.paskle.com:2083/api/user/';
const loginUrl: string = 'https://www.paskle.com:2083/api/login/';
const forgotUrl: string = 'https://www.paskle.com:2083/api/forgotpassword';
const passwordUrl: string = 'https://www.paskle.com:2083/api/updatepassword';

/*
  Generated class for the AuthserviceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthserviceProvider {

  constructor(public httpClient: HttpClient) {
    console.log('Hello AuthserviceProvider Provider');
  }

  forgot(user){
    return this.httpClient.put(forgotUrl, user)
    .toPromise()
    .then(data => {
      return data;
    })
    .catch(error => {
      console.log(error);
    })
  }
  
  otpCheck(user){
    return this.httpClient.post(forgotUrl, user)
    .toPromise()
    .then(data => {
      return data;
    })
    .catch(error => {
      console.log(error);
    })
  }
 
  newPassword(user){
    return this.httpClient.put(passwordUrl, user)
    .toPromise()
    .then(data => {
      return data;
    })
    .catch(error => {
      console.log(error);
    })
  }
 
  login(user){
    return this.httpClient.post(loginUrl, user)
    .toPromise()
    .then(data => {
      return data;
    })
    .catch(error => {
      console.log(error);
    })
  }
  
  registerUser(user){
    return this.httpClient.post(apiUrl, user)
    .toPromise()
    .then(data => {
      return data;
    })
    .catch(error => {
      console.log(error);
    })
  }

  getUserDetails () {
    return this.httpClient.get(apiUrl)
    .toPromise()
    .then((data: any) => {
      return data.results;
    })
    .catch(error => {
      console.log(error);
    })
  }
  
  getUserByID(user){
    console.log(user)
  	return this.httpClient.get(apiUrl + `${user}`)
  	.toPromise()
	.then(data => {
		return data;
	})
	.catch(error => {
		console.log(error);
	})
  }

  updateUserByID(user){
    console.log(user);
   return this.httpClient.put(apiUrl + `${user.id}`, user)
   .toPromise()
 .then(data => {
   return data;
 })
 .catch(error => {
   console.log(error);
 })
 }

 deleteUser(user){
  console.log(user);
 return this.httpClient.delete(apiUrl + `${user.id}`)
 .toPromise()
.then(data => {
 return data;
})
.catch(error => {
 console.log(error);
})
}


}



